<template>
  <div>
    <van-form @submit="onSubmit">
      <van-field
        v-model="password"
        type="password"
        name="password"
        label="新密码"
        placeholder="密码"
        :rules="[
          { required: true, message: '请填写密码' },
          {
            validator: passwordVal,
            message:
              '请输入最少6位，包括至少1个大写字母，1个小写字母，1个数字，1个特殊字符',
          },
        ]"
      />
      <van-field
        v-model="email"
        name="email"
        label="邮箱"
        placeholder="邮箱"
        :rules="[
          { required: true, message: '请填写邮箱地址' },
          { validator: mailVal, message: '请输入正确邮箱' },
        ]"
      >
        <template #button>
          <van-button
            @click="handleToVerify"
            native-type="button"
            type="primary"
            >发送验证码</van-button
          >
        </template>
      </van-field>
      <van-field
        native-type="submit"
        v-model="verify"
        type="digit"
        name="verify"
        label="验证码"
        :rules="[{ required: true, message: '请填写验证码' }]"
      />
      <div style="margin: 16px">
        <van-button round block type="info" native-type="submit"
          >修改</van-button
        >
      </div>
      <div style="margin: 16px">
        <van-button round type="info" block to="center">返回</van-button>
      </div>
    </van-form>
    <div class="login_link">
      <router-link to="/mine/login">立即登录</router-link>
      <router-link to="/mine/register">立即注册</router-link>
    </div>
  </div>
</template>

<script>
import { Dialog } from "vant";
export default {
  name: "findPassword",
  data() {
    return {
      password: "",
      email: "",
      verify: "",
    };
  },
  components: {
    [Dialog.Component.name]: Dialog.Component,
  },
  methods: {
    passwordVal(val) {
      return /^\S*(?=\S{6,})(?=\S*\d)(?=\S*[A-Z])(?=\S*[a-z])(?=\S*[!@#$%^&*? ])\S*$/.test(
        val
      );
    },
    mailVal(val) {
      return /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/.test(
        val
      );
    },

    handleToVerify() {
      this.axios({
        url: "/api2/users/verify?email=" + this.email,
      }).then((res) => {
        var status = res.data.status;
        if (status === 0) {
          Dialog.alert({
            closeOnClickOverlay: true,
            message: "验证码发送成功",
          }).then(() => {
            // on close
          });
        } else {
          Dialog.alert({
            closeOnClickOverlay: true,
            message: "验证码发送失败",
          });
        }
      });
    },

    onSubmit(values) {
      console.log("submit", values);
      this.axios({
        method: "post",
        url: "/api2/users/findPassword",
        data: {
          password: this.password,
          email: this.email,
          verify: this.verify,
        },
      }).then((res) => {
        var status = res.data.status;
        var This = this;
        if (status === 0) {
          Dialog.alert({
            closeOnClickOverlay: true,
            message: "修改密码成功",
          }).then(() => {
            // on close
            This.$router.push("/mine/login");
          });
        } else {
          Dialog.alert({
            closeOnClickOverlay: true,
            message: "修改密码失败",
          });
        }
      });
    },
  },
};
</script>

<style scoped>
.login_link {
  display: flex;
  justify-content: space-between;
}
.login_link a {
  text-decoration: none;
  margin: 0 5px;
  font-size: 12px;
  color: #e54847;
}
</style>